<template>
  <div id="ServiceDetail">
    <div class="banner container-fuild text-center">相关服务</div>
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-3" id="myScrollspy">
          <ul class="nav nav-tabs nav-stacked center-block" id="myNav">
            <p>特色服务</p>
            <li
              :class="item.id==id?'active':''"
              v-for="(item,index) in serviceNavList"
              :key="index"
            >
              <a :href="'#'+item.id">{{item.title}}</a>
            </li>
          </ul>
        </div>
        <div class="col-xs-12 col-sm-12 col-md-9 content wow zoomIn">
          <div class="content-block" v-for="(item,index) in serviceContentList" :key="index">
            <h2 :id="item.id">
              {{item.title}}
              <small>/ {{item.eng_title}}</small>
            </h2>
            <div v-html="item.content"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from "wowjs";
export default {
  name: "ServiceDetail",
  data() {
    return {
      id: "section-1",
      serviceNavList: [
        {
          id: "section-1",
          title: "食品解决方案"
        },
        {
          id: "section-2",
          title: "水质解决方案"
        },
        {
          id: "section-3",
          title: "土壤解决方案"
        },
        {
          id: "section-4",
          title: "环境解决方案"
        }
      ],
      serviceContentList: [
        {
          id: "section-1",
          title: "食品解决方案",
          eng_title: "食品安全",
          content:
            "<h3>采用吹扫捕集分析土味素和2-甲基异崁醇</h3><p>解决方案：采用美国OI公司的吹扫捕集Eclipse 4660分析饮用水中的土味素和2-甲基异崁醇</p><h3>食用油中总砷的全自动湿法消解</h3><p>解决方案：本方法参考GB/T 5009.11-2003，采用湿法消解的方式，利用全自动消解仪智能的将食用油消解完全。在此消解过程中体现全自动消解仪的简便、快速、智能。</p>"
        },
        {
          id: "section-2",
          title: "水质解决方案",
          eng_title: "水处理",
          content:
            "<h3>铂催化-非散射红外吸收法测定过氧化氢中的总有机碳</h3><p>解决方案：本文建立了测定过氧化氢产品中总有机碳的方法，用铂催化- 非散射红外吸收法测定过氧化氢中的总有机碳含量, 样品处理简单, 结果准确、可靠, 方法精密度高, 适用于过氧化氢产品中总有机碳的检测。</p><h3>采用加热的过硫酸盐氧化方法改进含有难以氧化物质的海水基体中的TOC分析</h3><p>解决方案：总有机碳（TOC）的分析已经广泛应用于检测大量的工业过程和污水排放过程。从历史上来讲，这些液流含有高浓度的盐，需要通过稀释进行样品的预处理。这样的稀释降低或者消除了盐对于化学氧化分析系统产生的影响，使高温燃烧系统需要的停机维护时间减少，能够得到更多可靠的数据</p><h3>比较在纯水测试中的总有机碳分析和美国药典的氧化物质测试</h3><p>解决方案：随着生物技术的发展，对水质越来越重视了。制药行业尤其关心痕量的有机污染物对于其产品以及生产这个产品的相关过程的有害性（例如，在产品之间的注射水及容器的清洁）。从而导致迫切地需要准确地分析这些水中的有机碳。</p><h3>水质中金属元素总量的全自动消解参考方法</h3><p>解决方案：参照HJ 677-2013的消解方法，建立水体样品的消解方法。通过全自动消解仪进行水质的先浓缩，后消解，自动、智能、安全的完成大批量水样的消解。方法中讨论了水体样品消解条件的优化，同时介绍了加快消解的仪器型号选择等。</p>"
        },
        {
          id: "section-3",
          title: "土壤解决方案",
          eng_title: "土壤净化",
          content:
            "<h3>吹扫捕集气相色谱-质谱法测定土壤中54 种挥发性有机物</h3><p>解决方案：建立了土壤中54 种挥发性有机物的吹扫捕集气相色谱质谱分析方法。54 种挥发性有机物检测限在0. 20 ～ 0. 72μg /kg 之间，平均加标回收率为92. 5%～ 112. 1%，各组分相对标准偏差为1. 4% ～ 8. 8%。本方法适用于批量样品的分析。</p><h3>土壤中微量元素（Pb，Zn，Cu等）的全自动湿法消解</h3><p>解决方案：参考土壤湿法消解标准，开发土壤的全自动湿法消解方法。本方法在各环境监测站进行研究、比对，实验证明此方法可将土壤样品自动消解完全，实验偏差在允许范围之内。本方法试用于Pb、Cr、Zn、Cu、K、Na、Ca、Mg等元素的湿法消解。</p>"
        },
        {
          id: "section-4",
          title: "环境解决方案",
          eng_title: "环境保护",
          content:
            "<h3>吹扫捕集-GC法检测卷烟包装材料中挥发性有机化合物</h3><p>解决方案：采用OI的Eclips4660与气相色谱联用，对卷烟包装材料中的苯系物，苯甲醛，和苯酚10种挥发性有机物进行检测。吹扫捕集技术作为样品的不使用有机溶剂的前处理方式，对环境不造成二次污染，具有取样量少，灵敏度高，检出限低，简便快速，结果准确等优点。</p><h3>吹扫捕集-气相色谱法测定空气中的甲醛含量</h3><p>解决方案：以蒸馏水作为吸收介质吸收空气中的甲醛，在常温下进行吹扫捕集，并用气相色谱法进行测定．同时对甲醛的稳定剂甲醇的用量、吹扫捕集的吹扫气流速和吹扫时间进行了优化，并在优化的实验条件下，测定了某新装修房间的甲醛含量．该方法对甲醛的检出浓度可达到０.04mg/ｍ３，且除了使用极微量的甲醇以外，几乎没用到有机溶剂，因此该方法具有检出限低、灵敏、快速、环保等特点．</p><h3>吹扫捕集-气相色谱法测定卷烟主流烟气中挥发酚</h3><p>解决方案：使用吹扫捕集-气相色谱法测定卷烟主流烟气中挥发酚的分析方法，使间甲酚和对甲酚分离并准确定量。本方法不但为烟草行业对卷烟主流烟气中酚的测定提供可靠的科学依据，而且也为环境监测、食品监测、及其他工业对挥发酚的检测提供有效参考。</p>"
        }
      ]
    };
  },
  mounted() {
    this.id = this.$route.params.id;
    var top = document.getElementById(this.id).offsetTop;
    $(window).scrollTop(top + 300);
    $("#myNav").affix({
      offset: {
        top: 300
      }
    });
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
ul.nav-tabs {
  width: 200px;
  margin-top: 40px;
  border-radius: 4px;
  background: #fff;
  z-index: 99999;
  border: 1px solid #474747;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.067);
}
ul.nav-tabs li {
  text-align: center;
  margin: 0;
  border-top: 1px solid #474747;
}
ul.nav-tabs p {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  background: #474747;
  margin: 0;
  padding: 10px 0;
}
ul.nav-tabs li:first-child {
  border-top: none;
}
ul.nav-tabs li a {
  margin: 0;
  padding: 8px 16px;
  border-radius: 0;
}
ul.nav-tabs li.active a,
ul.nav-tabs li.active a:hover {
  color: #fff;
  background: #474747;
  border: 1px solid #474747;
}
ul.nav-tabs li:first-child a {
  border-radius: 4px 4px 0 0;
}
ul.nav-tabs li:last-child a {
  border-radius: 0 0 4px 4px;
}
ul.nav-tabs.affix {
  top: 30px;
}
.content-block {
  margin: 50px 0;
}
.content-block > h2 {
  padding: 20px 0;
  border-bottom: 1px solid #ccc;
}
</style>



